$maxWidth: 1440;//ширина макета в граф. редакторе
$minWidth: 320;
$maxWidthContainer: 1180;//ширина ограничивающего контейнера без паддингов
$containerPaddings: 20;//сумма левого и правого паддингов ограничивающего контейнера
$maxContainerWidth: $maxWidthContainer + $containerPaddings;// итоговая ширина ограничивающего контейнера

// colors
$accent: #009245;
$text: #4D4D4D;
$bg: #333333;

// paths
	// use: background-image: url($icos + 'arrow-right.svg');
$img: '../img/';
$icos: '../img/icons/';
$svg: '../img/icons-svg/';


// media
$lg: em($maxWidthContainer);
$md: em(991);
$sm: em(767.98);
$xs: em(576);
$xss: em(480);
$xsss: em(375);
$min: em(360);
$mins: em(320);

// point for multilevel-pnel | размер, на котором multilevel-dropdown превращается в multilevel-panel и наоборот
$multilevelPoint: 1100px;

// Customization Bootstrap vars from "app/libs/bootstrap/scss/_variables.scss"
$grid-gutter-width: 30px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px);
$container-max-widths: (sm: 540px, md: 720px, lg: 960px, xl: 1170px);

$gutter: $grid-gutter-width;

//fonts
$baseFontSize: 17;// основной размер шрифта в макете
$reg: 'Articulat CF Normal', sans-serif;
$med: 'Articulat CF Medium', sans-serif;
$light: 'Articulat CF Light', sans-serif;


